.container{
    position: relative;
    width: 100wh;
    min-width: 1200px;
    height: 100vh;
    background-size: cover;
    display: flex;
    flex-direction: column;
}
body{
    background-color: #f8f8f8;
}
.mask{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: 0.5;
}

.row-wrapper{
    display: flex !important;
    align-items: center;
    justify-content: center;
    flex: 1;
    z-index: 1;
}

.intro-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    .content{
        width: 400px;
        color: #fff;
    }
    .title{
        margin-bottom: 20px;
        font-weight: 700;
        font-size: 48px;
        line-height: 1.5;
    }
    .description{
        margin: 0;
        font-size: 16px;
        color: #fff;
        letter-spacing: 0.45px;
        line-height: 40px;
    }
}

.login-container{
    width: 320px;
    height: 320px;
    margin: auto auto;
    padding: 36px;
    box-shadow: 0 0 100px rgba(0,0,0,.08);
    border-radius: 6px;
    .login-text{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 16px;
        span{
            margin-left: 10px;
            vertical-align: text-bottom;
            font-size: 16px;
            display: inline-block;
            font-weight: 700;
            color: #1890ff;
        }
    }
}

.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
}
.copyright{
    font-size: 14px;
    color: rgba(0,0,0,.45);
    line-height: 1.5;
    text-align: right;
}

